import {registerBlockType} from '@wordpress/blocks';
import './style.scss';
import metadata from './block.json';

/**
 * Internal dependencies
 */
import Edit from './edit';
import Save from './save';

// icon
import icon from './icon';

import saveDep from './deprecated';

/**
 * Block Registration
 */
registerBlockType(metadata, {
    icon: {
        src: icon,
        foreground: '#3E58E1',
    },
    edit: Edit,
    save: Save,
    deprecated: [
        {
            "attributes": {
                "style": {
                    "type": "object",
                    "default": {
                        "border": {
                            "color": "#CFCABE",
                            "radius": "3px",
                            "style": "dash",
                            "width": "1px"
                        }
                    }
                },
                "uniqueId": {
                    "type": "string"
                },
                "border": {
                    "type": "object",
                    "default": {
                        "color": "#72aee6",
                        "style": "solid",
                        "width": "2px"
                    }
                },
                "paddings": {
                    "type": "object",
                    "default": {
                        "top": "10px",
                        "left": "20px",
                        "right": "20px",
                        "bottom": "10px"
                    }
                },
                "margins": {
                    "type": "object",
                    "default": {
                        "top": "0px",
                        "bottom": "15px"
                    }
                },
                "borderRadius": {
                    "type": "number",
                    "default": 0
                },
                "qIconText": {
                    "type": "string",
                    "default": "Q"
                },
                "qIconColor": {
                    "type": "string",
                    "default": "#fff"
                },
                "qIconBg": {
                    "type": "string",
                    "default": "#505050"
                },
                "aIconText": {
                    "type": "string",
                    "default": "A"
                },
                "aIconColor": {
                    "type": "string",
                    "default": "#fff"
                },
                "aIconBg": {
                    "type": "string",
                    "default": "#f5a623"
                },
                "heading": {
                    "type": "string",
                    "default": "Accordion Heading"
                },
                "subheading": {
                    "type": "string",
                    "default": ""
                },
                "subheadingColor": {
                    "type": "string"
                },
                "headingTag": {
                    "type": "string",
                    "default": "h4"
                },
                "headingColor": {
                    "type": "string"
                },
                "headerBg": {
                    "type": "string"
                },
                "headingIconImageUrl": {
                    "type": "string",
                    "default": ""
                },
                "headingIconAlt": {
                    "type": "string",
                    "default": ""
                },
                "showHeadingIcon": {
                    "type": "boolean",
                    "default": false
                },
                "showIcon": {
                    "type": "boolean",
                    "default": true
                },
                "anchorLinkShow": {
                    "type": "boolean",
                    "default": false
                },
                "iconClass": {
                    "type": "string",
                    "default": "plus-alt2"
                },
                "iconPosition": {
                    "type": "string",
                    "default": "aab_right_icon"
                },
                "iconFontSize": {
                    "type": "number",
                    "default": 25
                },
                "iconColor": {
                    "type": "string"
                },
                "iconBackground": {
                    "type": "string"
                },
                "iconBorder": {
                    "type": "object",
                    "default": {
                        "width": "1px",
                        "style": "solid",
                        "color": "transparent"
                    }
                },
                "iconBorderRadius": {
                    "type": "string",
                    "default": "50%"
                },
                "bodyBg": {
                    "type": "string"
                },
                "makeActive": {
                    "type": "boolean",
                    "default": false
                },
                "id": {
                    "type": "string"
                },
                "linkedAccordion": {
                    "type": "boolean",
                    "default": false
                },
                "link": {
                    "type": "string",
                    "default": "#"
                },
                "tab": {
                    "type": "boolean",
                    "default": false
                },
                "disableAccordion": {
                    "type": "boolean",
                    "default": false
                },
                "feedbackShow": {
                    "type": "boolean",
                    "default": false
                },
                "feedbacLabel": {
                    "type": "string",
                    "default": "Was this answer helpful?"
                },
                "yesBtn": {
                    "type": "string",
                    "default": "Yes"
                },
                "noBtn": {
                    "type": "string",
                    "default": "No"
                },
                "counterShow": {
                    "type": "boolean",
                    "default": false
                },
                "incNumber": {
                    "type": "number",
                    "default": 1
                },
                "anchorPosition": {
                    "type": "string",
                    "default": "aab_right_link"
                },
                "faqSchema": {
                    "type": "boolean",
                    "default": false
                },
                "QaStyle": {
                    "type": "boolean",
                    "default": false
                },
                "customCSS": {
                    "type": "string",
                    "default": ""
                },
                "headingBorder": {
                    "type": "object",
                    "default": {
                        "color": "undefined",
                        "style": "solid",
                        "width": "0px"
                    }
                },
                "bodyBorder": {
                    "type": "object",
                    "default": {
                        "top": {
                            "color": "#72aee6",
                            "style": "solid",
                            "width": "2px"
                        }
                    }
                }
            },
            migrate: (attributes) => {
                return {
                    ...attributes,
                    border: {
                        color: attributes.border.color,
                        style: attributes.border.style === "solid" ? "solid" : attributes.border.style,
                        width: attributes.border.width === "2px" ? "1px" : attributes.border.width
                    },
                    paddings: {
                        top: attributes.paddings.top === "10px" ? "" : attributes.paddings.top,
                        right: attributes.paddings.right === "20px" ? "" : attributes.paddings.right,
                        left: attributes.paddings.left === "20px" ? "" : attributes.paddings.left,
                        bottom: attributes.paddings.bottom === "10px" ? "" : attributes.paddings.bottom
                    },
                    margins: {
                        top: attributes.margins.top === "0px" ? "0px" : attributes.margins.top,
                        right: attributes.margins.right === "15px" ? "" : attributes.margins.right,
                        left: attributes.margins.left === "15px" ? "" : attributes.margins.left,
                        bottom: attributes.margins.bottom === "15px" ? "15px" : attributes.margins.bottom
                    },
                    headingTag: attributes.headingTag === "h4" ? "h5" : attributes.headingTag,
                    headingColor: attributes.headingColor || '',
                    headerBg: attributes.headerBg || '',
                    iconFontSize: attributes.iconFontSize === 25 ? 23 : attributes.iconFontSize,
                    iconBorder: {
                        width: attributes.iconBorder.width === "50%" ? "0px" : attributes.iconBorder.width,
                        style: attributes.iconBorder.style === "solid" ? "solid" : attributes.iconBorder.style,
                        color: attributes.iconBorder.color === "transparent" ? "transparent" : attributes.iconBorder.color
                    }
                };
            },
            save: saveDep,
        },
    ],
});

